<template>
  <v-container grid-list-xl grid-list-lg class="content-index">
    <div>
      <span class="text-h4 font-weight-bold">  </span>
      <br />
      <span class="text-h4 font-weight-light"
        >   .</span
      >
    </div>
    <br />
    <v-layout>
      <v-flex xl6 lg9 sm12>
        <validation-observer v-slot="{ handleSubmit, reset }">
          <v-form
            @submit.prevent="handleSubmit(onSubmit)"
            @reset.prevent="reset"
          >
            <div class="text-h5"> </div>
            <div class="text-h6 mb-2">   .</div>
            <validation-provider
              name="projectName"
              :rules="{ required: true, min: { length: 4 } }"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.projectName"
                :error-messages="errors[0]"
                required
                single-line
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <div class="text-h5"> </div>
            <div class="text-h6 mb-2">
                   .
            </div>
            <validation-provider
              name="description"
              :rules="{ max: { length: 30 } }"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.description"
                :error-messages="errors[0]"
                counter="30"
                maxlength="30"
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <div class="text-h5"> </div>
            <div class="text-h6 mb-2">
                ,  ( 0)  .
            </div>
            <validation-provider
              name="topic"
              rules="required"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.topic"
                :error-messages="errors[0]"
                required
                single-line
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <div class="text-h5"> </div>
            <div class="text-h6 mb-2">
                 .    
              .
            </div>
            <validation-provider
              name="teamName"
              rules="required"
              v-slot="{ errors }"
            >
              <v-text-field
                v-model="form.teamName"
                :error-messages="errors[0]"
                required
                single-line
                solo
                class="mb-2"
              ></v-text-field>
            </validation-provider>
            <v-layout class="text-center">
              <v-btn type="reset" color="info" tile @click="onClear"
                ></v-btn
              >
              <v-spacer></v-spacer>
              <v-btn type="submit" color="success" tile> </v-btn>
            </v-layout>
          </v-form>
        </validation-observer>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import {
  ValidationObserver,
  ValidationProvider,
  extend,
  localize,
} from 'vee-validate'
import { required, min, max } from 'vee-validate/dist/rules'

export default {
  components: {
    ValidationObserver,
    ValidationProvider,
  },
  props: {
    user: {
      type: Object,
    },
  },
  data() {
    return {
      form: {
        projectName: '',
        description: '',
        topic: '',
        teamName: '',
        user: this.user,
      },
      message: {
        required: '{_field_}  .',
        min: '{_field_}   {length}  .',
        max: '{_field_}   {length} .',
      },
    }
  },
  mounted() {
    localize({
      ko: {
        names: {
          projectName: ' ',
          description: ' ',
          topic: ' ',
          teamName: ' ',
        },
        messages: {
          required: this.message.required,
          min: this.message.min,
          max: this.message.max,
        },
      },
    })
    localize('ko')
    extend('required', required)
    extend('min', min)
    extend('max', max)
  },
  methods: {
    async onSubmit() {
      try {
        const result = await this.$http.post('/project', this.form)
        if (!result) {
          throw new Error('  .')
        }
        alert('  .')
        this.$router.push({ name: 'user' })
      } catch (e) {
        alert(e.response.data.message)
      }
    },
    onClear() {
      this.form.projectName = ''
      this.form.description = ''
      this.form.topic = ''
      this.form.teamName = ''
    },
  },
}
</script>
